import {Accordion, Button, Collapse, Container, Table} from "react-bootstrap";
import {useState} from "react";



export default function MusicList(){
    const itemcss = {
        color:'white',
        height: '10vh',
        display:'flex',
        alignItems: 'center'
    }
    const music = JSON.parse(window.localStorage.getItem("music"))
    console.log(music)
    const [current,setCurrent] = useState(0);

    const clickListener = (e) => {
        console.log(e.target.value);
        setCurrent(e.target.value);
    }

    return(
        <Container fluid style={{background: 'linear-gradient(#ff74a4 0%, #9f6ea3 100%)', height:"250vh", width: '100vw',display:'flex',flexDirection:'column',justifyContent:'center',alignItems: 'center'}}>


            <Container fluid className="bg-white mt-lg-3" style={{display:'flex',flexDirection:'column',height: "30%", width:'40%', borderRadius: '32px', boxShadow: '0 6px 15px rgba(0,0,0,0.15)'}}>
                <img src={music[current][3] ? music[current][3] : "asset/blank.png"} className="mx-auto d-flex mt-lg-5 mb-3" style={{height:'50%', width:"70%", borderRadius: '15px', boxShadow: '0 6px 15px rgba(0,0,0,0.15)'}}/>
                <h3 className="mx-auto">{music[current][1]}</h3>
                <h5 className="mx-auto">{music[current][0]}</h5>
                <Container fluid className=" mx-auto g-5" style={{width: '95%', height:'20%',borderRadius: '32px', boxShadow: '0 6px 15px rgba(0,0,0,0.15)',textAlign:'center' }}>
                    <Container fluid className="mt-3" style={{display:'flex',justifyContent:'space-around', }}>
                        <audio controls="controls" src={music[current][2]} />
                    </Container>
                </Container>
            </Container>


            <Table hover className="mt-lg-5" style={{width:'80%', height:"50%"}}>
                <thead>
                <tr>
                    <th>#</th>
                    <th>artist</th>
                    <th>track name</th>
                    <th>link</th>
                </tr>
                </thead>
                <tbody>
                {music.map((m,index)=> {
                    return(
                        <tr key={index}>
                            <td>{index+1}</td>
                            <td>{m[0]}</td>
                            <td>{m[1]}</td>
                            <td><Button value={`${index}`} onClick={clickListener}>#</Button></td>
                        </tr>);
                })}
                </tbody>
            </Table>
        </Container>
    );
}